import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { RouteLink } from '@Green-Dot-Corporation/eureka-lib-router-utils';

// Styles
import './styles/header.scss';

export default class Header extends PureComponent {
    static defaultProps = {
        appConfig: {},
    };

    static propTypes = {
        appConfig: PropTypes.object,
    };

    render() {
        return (
            <header className={cx(this.baseCls)}>
                {this.renderLogo()}{' '}
                <div className={`${this.baseCls}__links`}>
                    <RouteLink to="/board">Board</RouteLink>
                    <RouteLink to="/game">Game</RouteLink>
                </div>
            </header>
        );
    }

    renderLogo = () => {
        const logoPath = this.getLogoPath();

        return (
            <div className={`${this.baseCls}__logo-container`}>
                <img
                    className={`${this.baseCls}__logo`}
                    src={logoPath}
                    alt="logo"
                />
            </div>
        );
    };

    getLogoPath = () => {
        const { appConfig } = this.props;
        const { resourcePath = '', branding = {} } = appConfig;
        const { mediaPath = '' } = branding;

        return `${resourcePath}${mediaPath}/logo.svg`;
    };

    baseCls = 'demo-header';
}
